@import '../../global.scss';

.headerContainer {
    height: 80px;
    width: 100%;
    position: fixed;
    top: -80px;
    z-index: 100;
    display: flex;
    align-items: center;
    transition: all var(--transition-normal);
    backdrop-filter: blur(8px);

    @media (min-width: 768px) {
        height: 100px;
        top: -100px;
    }

    .desktopHeader {
        display: none;
        width: 100%;
        padding: 0 var(--spacing-md);
        justify-content: space-between;
        align-items: center;

        @media (min-width: 1024px) {
            display: flex;
            padding: 0 var(--spacing-lg);
        }

        img {
            cursor: pointer;
            height: 40px;
            width: auto;
            transition: transform var(--transition-normal);

            &:hover {
                transform: scale(1.05);
            }
        }

        nav {
            display: flex;
            gap: var(--spacing-md);

            button {
                position: relative;
                font-size: 1rem;
                font-weight: 500;
                color: var(--white);
                padding: var(--spacing-xs) var(--spacing-sm);
                transition: all var(--transition-normal);

                &::after {
                    content: '';
                    position: absolute;
                    bottom: 0;
                    left: 50%;
                    width: 0;
                    height: 2px;
                    background: currentColor;
                    transition: all var(--transition-normal);
                    transform: translateX(-50%);
                }

                &:hover {
                    transform: translateY(-2px);

                    &::after {
                        width: 100%;
                    }
                }
            }
        }
    }

    .mobileHeader {
        display: flex;
        width: 100%;
        padding: 0 var(--spacing-md);
        justify-content: space-between;
        align-items: center;
        height: 64px;

        @media (min-width: 1024px) {
            display: none;
        }

        img {
            cursor: pointer;
            height: 40px;
            width: auto;
            transition: transform var(--transition-normal);

            &:hover {
                transform: scale(1.05);
            }
        }

        nav {
            position: fixed;
            top: 0;
            right: -100%;
            height: 100vh;
            width: 100%;
            background: var(--white);
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: var(--spacing-xl);
            transition: right var(--transition-normal);
            z-index: 99;
            padding: 80px 0;

            button {
                font-size: 1.25rem;
                font-weight: 500;
                color: var(--grey);
                padding: var(--spacing-md);
                transition: all var(--transition-normal);
                width: 100%;
                text-align: center;

                &:hover {
                    transform: translateX(10px);
                    color: var(--aerovect-blue);
                }
            }
        }
    }

    .mobileOpen {
        .hamburger-react {
            z-index: 100;

            div {
                background: var(--grey) !important;
            }
        }

        nav {
            right: 0;
        }
    }
}

.showHeader {
    top: 0;
    box-shadow: var(--shadow-sm);
}

.hasColor {
    background: rgba(255, 255, 255, 0.9);

    .desktopHeader {
        nav {
            button {
                color: var(--grey);

                &:hover {
                    color: var(--aerovect-blue);
                }
            }
        }
    }

    .mobileOpen {
        .hamburger-react {
            div {
                background: var(--grey) !important;
            }
        }
    }
}

.noColor {
    background: rgba(0, 0, 0, 0.8);

    .hamburger-react {
        div {
            background: var(--white) !important;
        }
    }
}

.noBackground {
    background: none !important;
    backdrop-filter: none !important;
    box-shadow: none !important;
}

// Animation for header items
@keyframes slideDown {
    from {
        opacity: 0;
        transform: translateY(-20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.desktopHeader nav button {
    animation: slideDown var(--transition-normal) forwards;
    animation-delay: calc(var(--i, 0) * 0.1s);
    opacity: 0;
}