@import '../../global.scss';

.footerContainer {
    width: 100%;
    // background: red;
    // position: fixed;
    background: $grey;
    z-index: 5;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 60px $page-padding-x-dl 40px;
    box-sizing: border-box;

    @media (min-width: $mobile) {
        padding: 40px $page-padding-x-m 30px;
    }

    @media (min-width: $tablet-small) {
        padding: 50px $page-padding-x-ts 35px;
    }

    @media (min-width: $tablet-large) {
        padding: 60px $page-padding-x-tl 40px;
    }

    @media (min-width: $desktop-small) {
        padding: 60px $page-padding-x-ds 40px;
    }

    @media (min-width: $desktop-large) {
        padding: 60px $page-padding-x-dl 40px;
    }

    .footerUpper {
        display: flex;
        width: 100%;
        justify-content: space-between;
        padding-bottom: 40px;
        border-bottom: 0.5px solid #939393;
        flex-direction: row;
        gap: 40px;

        @media (min-width: $mobile) {
            flex-direction: column;
            padding-bottom: 30px;
            gap: 30px;
        }

        @media (min-width: $tablet-small) {
            flex-direction: column;
            padding-bottom: 35px;
            gap: 35px;
        }

        @media (min-width: $tablet-large) {
            flex-direction: row;
            padding-bottom: 40px;
            gap: 40px;
        }

        @media (min-width: $desktop-small) {
            flex-direction: row;
            padding-bottom: 40px;
            gap: 40px;
        }

        @media (min-width: $desktop-large) {
            flex-direction: row;
            padding-bottom: 40px;
            gap: 40px;
        }

        .footerBlock {
            flex: 1;
            margin-bottom: 0px;

            @media (min-width: $mobile) {
                margin-bottom: 0px;
            }

            @media (min-width: $tablet-small) {
                margin-bottom: 0px;
            }

            @media (min-width: $tablet-large) {
                margin-bottom: 0px;
            }

            @media (min-width: $desktop-small) {
                margin-bottom: 0px;
            }

            @media (min-width: $desktop-large) {
                margin-bottom: 0px;
            }
        }

        .logoBlock {
            img {
                margin-bottom: 24px;
            }

            p {
                color: #B4D2FF;
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 150%;
                /* or 24px */

                letter-spacing: -0.015em;
            }
        }

        h5 {
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 150%;
            letter-spacing: -0.015em;
            color: #FFFFFF;
            margin-bottom: 18px;
        }
    }

    .addressBlock {
        address {
            font-style: normal;
            font-weight: 300;
            font-size: 16px;
            line-height: 150%;
            letter-spacing: -0.015em;
            color: #FFFFFF;
        }
    }

    .contactBlock {
        .contactRow:not(:last-child) {
            margin-bottom: 12px;
        }

        .contactRow {
            display: flex;
            align-items: center;

            img {
                margin-right: 14px;
                width: 16px;
                height: 16px;
            }

            a {
                color: white;
                text-decoration: none;
                font-style: normal;
                font-weight: 300;
                font-size: 16px;
                line-height: 150%;
                letter-spacing: -0.015em;
            }
        }
    }

    .footerBottom {
        margin-top: 24px;
        display: flex;
        justify-content: space-between;
        flex-direction: row;

        @media (min-width: $mobile) {
            flex-direction: column-reverse;
            margin-top: 20px;
        }

        @media (min-width: $tablet-small) {
            flex-direction: column-reverse;
            margin-top: 20px;
        }

        @media (min-width: $tablet-large) {
            flex-direction: row;
            margin-top: 24px;
        }

        @media (min-width: $desktop-small) {
            flex-direction: row;
            margin-top: 24px;
        }

        @media (min-width: $desktop-large) {
            flex-direction: row;
            margin-top: 24px;
        }

        small {
            font-style: normal;
            font-weight: 300;
            font-size: 16px;
            line-height: 150%;
            letter-spacing: -0.015em;
            color: #FFFFFF;
            margin-top: 0;
            justify-content: initial;

            @media (min-width: $mobile) {
                justify-content: center;
                margin-top: 20px;
            }

            @media (min-width: $tablet-small) {
                justify-content: center;
                margin-top: 20px;
            }

            @media (min-width: $tablet-large) {
                justify-content: initial;
                margin-top: 0;
            }

            @media (min-width: $desktop-small) {
                justify-content: initial;
                margin-top: 0;
            }

            @media (min-width: $desktop-large) {
                justify-content: initial;
                margin-top: 0;
            }
        }

        .social {
            display: flex;
            align-items: center;
            justify-content: initial;

            @media (min-width: $mobile) {
                justify-content: center;
            }

            @media (min-width: $tablet-small) {
                justify-content: center;
            }

            @media (min-width: $tablet-large) {
                justify-content: initial;
            }

            @media (min-width: $desktop-small) {
                justify-content: initial;
            }

            @media (min-width: $desktop-large) {
                justify-content: initial;
            }

            a:not(:last-child) {
                margin-right: 40px;
            }
        }
    }
}

.footerPrivacyLink {
    color: white;
    text-decoration: none;
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: -0.015em;
    margin-left: 0;
    margin-top: 8px;
    display: inline-block;
    transition: color 0.2s;

    &:hover {
        color: #B4D2FF;
        text-decoration: underline;
    }
}